@font-face {
  font-family: 'Bold';
  src: url('../font/CertoSans-Bold.eot');
  src: url('../font/CertoSans-Bold.eot?#iefix') format('embedded-opentype'),
      url('../font/CertoSans-Bold.woff2') format('woff2'),
      url('../font/CertoSans-Bold.woff') format('woff'),
      url('../font/CertoSans-Bold.ttf') format('truetype'),
      url('../font/CertoSans-Bold.svg#CertoSans-Bold') format('svg');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

