//COLORS



:root {
  --color-black:#000000;
  --color-white:#ffffff;
  --color-orange:#FF7F00;
  --color-drak-blue:#0F2837;
  --color-light-blue:#91B9B4;
  --font-book:'Book', Fallback, sans-serif;
  --font-bold:'Bold', Fallback, sans-serif;
  --font-medium:'Medium', Fallback, sans-serif;
  --font-regular:'Regular', Fallback, sans-serif;
  --font-light:'Light', Fallback, sans-serif;
}
