// -----------------------------------------------------------------------------
// This file contains all styles related to the footer of the site/application.
// -----------------------------------------------------------------------------

.footer{
    background-color: var(--color-black);
    padding: 6rem 4rem;
    height: 30rem;
    width: 100%;
    color: var(--color-white);

    &__wrap{
        display: flex;
        flex-wrap: wrap;
    }

    &__item{
        flex: 1;
        &:not(:last-child){
            margin-inline-end: 1.5rem;
        }

        ul{
            margin-top: -.8rem;
            li{
                    line-height: 3;
                    font-size: 1.2rem;
                a{
                    color: var(--color-white);
                    &:hover{
                        color: var(--color-orange);
                    }
                }
            }
        }
    }

    &__follow{
        span{
            display: block;
            margin-bottom: 1.5rem;

        }

        a{
            &:not(:last-child){
                margin-inline-end: 8px;
            }
        }

        .icon{
            height: 2.2rem;
            width: 2.2rem;
           
        }
    }

    &__download{
        span{
            display: block;
            margin-bottom: 1.5rem;
        }

        img{
            display: block;
            margin-bottom: 1.5rem;
        }
    }
}

.ministry{
    margin-top: 3rem;
    img{
        &:first-child{
            margin-inline-end: 3rem;
        }
    }
}

.copyRight{
    margin-top: 3rem;
    span{
        display: block;
        font-size: 1rem;
    }
}

